<template>
	<div id="position">
		<Header></Header>
		<div id="container" v-loading='loading' element-loading-text="定位中，请稍等" element-loading-spinner="el-icon-loading" element-loading-background="rgba(0, 0, 0, 0.8)"></div>
	</div>
</template>
<script>
	import Header from './header'
	import BMap from 'BMap'
	import Footer from './footer'
	export default {
		mounted() {
			var this_ = this;
			var map = new BMap.Map("container");
			// 创建地图实例  
			var point = new BMap.Point(116.404, 39.915);
			// 创建点坐标  
			map.centerAndZoom(point, 1000);
			// 初始化地图，设置中心点坐标和地图级别
			var geolocation = new BMap.Geolocation();
			geolocation.getCurrentPosition(function(r) {
				if(this.getStatus() == BMAP_STATUS_SUCCESS) {
					var mk = new BMap.Marker(r.point);
					map.addOverlay(mk);
					map.panTo(r.point);
					alert('您的位置：' + r.address.province + ',' + r.address.city);
					this_.loading = false;
				} else {
					alert('failed' + this.getStatus());
				}
			});
		},
		components: {
			Header,
			Footer
		},
		data() {
			return {
				loading: true
			}
		}

	}
</script>

<style>
	#position #container {
		height: 800px;
	}
</style>


