C：CORS：请看我的另一篇文章。
					`,
						date: '2010-05-27 16:16:07'
					}
				]

			}
		}
	}
</script>

<style>
	#text .text {
		font-size: 14px;
	}
	
	#text .item {
		margin-bottom: 18px;
	}
	
	#text  #text_  .box-card {
		width: 70%;
		margin: 0 auto;
		margin-top: 20px;
	}
</style>


