const state = {
	isplay:false,
	isanimate:false
}

const mutations = {
	played(state){
		state.isplay = true;
	}
}

const getters = {
	isplay(state){
		return state.isplay;
	},
	isanimate(state){
		return state.isanimate;
	}
}

export default{
	state,
	mutations,
	getters
}



