<template>
	<div class="block">
	<div id="sowingmap">
		<el-carousel :height='bannerHeight+"px"' id='controls_sowingmap'>
			<!--<el-carousel-item v-for="item in 4" :key="item">
				<h3>{{ item }}</h3>
			</el-carousel-item>-->
			<el-carousel-item>
				<img width="1920"  src="../assets/img1.jpg" />
			</el-carousel-item>

			<el-carousel-item>
				<img width="1920"  src="../assets/img2.jpg" />
			</el-carousel-item>
			<el-carousel-item>
				<img width="1920" src="../assets/img3.jpg" />
			</el-carousel-item>
			
		</el-carousel>
	</div>
	</div>
</template>

<script>
	export default{
		//要写一个让banner图自适应的js
		mounted(){
			window.addEventListener('resize',function(){
				this.bannerHeight = window.innerWidth*0.4;
			})
		},
		data(){
			return {
				bannerHeight:400
			}
		}
	}
</script>

<style>
	#block #controls_sowingmap{
		background-color: black;
	}
	#block  img{
		width:100%;height:auto;
	}
</style>


