<template>
	<div id="mytextdetail">
		<Header></Header>
		<div id="over">
			<div id="root">
				<div id="train" v-for='(val, key, index) in myblog' class="animated">
					<el-card class="box-card" :id="val.id">
						<div slot="header">
							<span id="card_name">{{val.title}}</span>
							<div id="write_date">
								<i class="el-icon-edit"></i>
								<span>{{val.date}}</span>
							</div>
						</div>
						<div id="text_context">
							{{val.context}}
						</div>

						<div id="footer">
							<div id="footer_left" v-if="myblog[key-1]" @click="previous_(100)">
								<{{myblog[key-1].title}}</div>
									
									<div id="footer_right" v-if="myblog[key+1]" @click="next_(100)">{{myblog[key+1].title}}></div>
							</div>

					</el-card>
					</div>
				</div>
			</div>
			<Footer></Footer>
		</div>
</template>

<script>
	import Footer from './footer'
	import Header from './header'
	import { mapGetters } from 'vuex'
	export default {
		computed: mapGetters([]),
		mounted() {
			var that = this;
			this.id_ = this.$route.query.id;
			this.$http.get('/api/other.html', {}).then(function(res) {
				that.myblog = res.data;
				that.resize();
				setTimeout(function() {
					that.resize();
				}, 0)
				window.onresize = that.resize;
				that.next_(that.id_);
				setTimeout(function() {
					var height = $("#" + that.id_).outerHeight();
					$("#root").css('height', height);

				}, 100)

			}).catch(function(err) {
				console.log(err);
			})
		},
		methods: {
			previous_(id) {
				if(id == 100) {
					id = 1;
					var newnumber = this.id_ - 1;
					this.id_ = newnumber;
					var height = $("#" + newnumber).outerHeight();
					$("#root").css('height', height);
				}
				var height = window.innerWidth;
				/*var left = '-'+height+'px';*/
				var left = height;
				var marginleft = parseInt($('#root').css('margin-left'));

				var new_ = marginleft + left;
				var new__ = new_ + 'px'

				$('#root').css('margin-left', new__);
				window.scrollTo(0, 0);
			},
			next_(id) {
				if(id == 100) {
					id = 1;
					var newnumber = this.id_ + 1;
					this.id_ = newnumber;
					var height = $("#" + newnumber).outerHeight();
					$("#root").css('height', height);
				}
				if(typeof(id) == 'object') {
					id = 1;
				}

				if(id == 0) {
					id = 0;
				}

				var height = window.innerWidth;
				/*var left = '-'+height+'px';*/
				var left = height;
				var marginleft = parseInt($('#root').css('margin-left'));
				var new_ = marginleft - left * id;
				var new__ = new_ + 'px'

				$('#root').css('margin-left', new__);
				window.scrollTo(0, 0);
			},
			resize() {
				var height = window.innerHeight;
				var width = parseInt(window.innerWidth);
				var ss = width * 0.7;
				var card_width = width * 0.7 + 'px';
				var card_left = width * 0.15 + 'px';
				var card_right = width * 0.15 + 'px';
				$(".box-card").css('width', card_width);
				$(".box-card").css('margin-left', card_left);
				$(".box-card").css('margin-right', card_right);
			}
		},
		components: {
			Header,
			Footer
		},
		data() {
			return {
				id_: '',
				isanimated: true,
				text_id: '',
				ifshow_left: true,
				ifshow_right: true,
				previous: '我的上一个',
				next: '我的下一个',
				detail_blog: {},
				myblog: []
			}
		}
	}
</script>

<style>
	#mytextdetail {
		overflow: hidden;
	}
	#mytextdetail #root {
		width: 600000px;
		transition: all 1s;
		overflow: hidden;
		display: flex;
		align-items: flex-start;
	}
	
	#mytextdetail #train {
		display: inline-block;
	}
	
	#mytextdetail #footer {
		border-top: 1px solid #eeeeee;
		margin-top: 20px;
	}
	
	#mytextdetail #footer_left {
		margin-top: 20px;
		float: left;
		cursor: pointer;
		color: cornflowerblue;
	}
	
	#mytextdetail #footer_right {
		float: right;
		margin-top: 20px;
		cursor: pointer;
		color: cornflowerblue;
	}
	
	#mytextdetail #text_context {
		white-space: pre-line;
	}
	
	#mytextdetail #write_date {
		opacity: 0.5;
	}
	
	#mytextdetail #card_name {
		display: block;
		padding-bottom: 20px;
	}
	
	#mytextdetail .el-card__header {
		text-align: center;
	}
	
	#mytextdetail .text {
		font-size: 14px;
	}
	
	#mytextdetail .item {
		margin-bottom: 18px;
	}
	
	#mytextdetail .box-card {
		width: 600px;
		margin: 20px auto;
		padding: 20px 0 20px 0;
		top: 20px;
	}
	
	#mytextdetail .animated {
		animation: test 1s infinite;
		animation-iteration-count: 1;
		animation-fill-mode: forwards;
	}
	
	@keyframes test {
		0% {
			opacity: 0;
			/*	-webkit-transform: translateY(-2000px);
			-ms-transform: translateY(-2000px);
			transform: translateY(-2000px)*/
		}
		100% {
			opacity: 1;
			/*	-webkit-transform: translateY(0);
			-ms-transform: translateY(0);
			transform: translateY(0)*/
		}
	}
</style>


